import VueRouter from 'vue-router'
import learn from './components/Learn'
import table from './components/Table'
import training from './components/Training'
export default new VueRouter({
  routes: [
    {
      path: '',
      component: learn
    },
    {
      path: '/table',
      component: table
    },
    {
      path: '/training',
      component: training
    },
    
  ],
  mode: 'history'
})