<template>
    <div class="wrap">
       <h1>Впишите в поля 2-ю и 3-ю формы глагола</h1>
       <p class="taskDescr">Ecли глагол во 2-й или 3-й форме имеет разные определения для ед.ч и мн.ч, разделяйте эти определения '/'. Например: was/were </p>
       <v-layout>
        <v-flex xs12 sm6 offset-sm3>
          <v-card class='learnCard'>
           <span class="counter">{{i}}/255</span>
           <v-card-title primary-title>
             <div class="searchNum">
              <v-text-field
                label="1-255"
                single-line
                Regular
                v-model="num"
                class="searchNumInp"
              ></v-text-field ><v-btn @click="toFind" class="searchNumBtn" dark color="indigo">Перейти</v-btn>
            </div>
               
              <div dark class="red mainWord">
                <h3 class="headline mb-0">{{arr3[i][0]}}</h3>
                <h4>({{arr3[i][3]}})</h4>
              </div>
            </v-card-title>
            <v-card-text>
              <h4>Подсказка:
                <v-btn dark color="indigo" @click.stop="dialog = true">
                   <v-icon>help</v-icon>
                </v-btn>
              </h4>
              <v-dialog
                  v-model="dialog"
                  max-width="290"
              >
               <v-card>
               <v-card-title class="headline">Подсказка:</v-card-title>
              <v-card-text>
                 <p>2-я форма:<b>{{ arr3[i][1] }}</b></p>
                 <p>3-я форма:<b>{{ arr3[i][2] }}</b></p>
              </v-card-text>
              <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="green darken-1"
                flat="flat"
                @click="dialog = false"
              >
                Закрыть
              </v-btn>
              </v-card-actions>
              </v-card>
            </v-dialog>
            <v-form ref="form"
              v-model="valid"
              lazy-validation>
               <v-text-field  label="2-я форма"  
               :rules="field1Rules">
               </v-text-field>
               <v-text-field  label="3-я форма" 
               :rules="field2Rules">
               </v-text-field>
               <v-btn color="success" :disabled="!valid"  @click="validate">Проверить</v-btn>
              </v-form>
            </v-card-text>
          </v-card>
        </v-flex>
       </v-layout>
    </div>
</template>
<script>
import json from '../verbs.json'
export default {
   data() {
      return {
        i: 1,
        arr3: '',
        num:'',
        dialog: false,
        valid: true,
        field1Rules: [
          v => !!v || 'Заполните поле',
          v => v === this.arr3[this.i][1] ||
              'Неправильно'
        ],
        field2Rules: [
          v => !!v || 'Заполните поле',
          v => v === this.arr3[this.i][2] ||
              'Неправильно'
          
        ],
        
      }
   },
   created() {
      let strFromJson = JSON.stringify(json);
      this.arr3 = JSON.parse(strFromJson);
   },
    methods: {
       validate () {
        if (this.$refs.form.validate()) {
           this.i +=1;
           this.$refs.form.reset();
           if (this.i == 256) {
              this.i = 1;
           }
        } 
      },
       toFind() {
         if (this.num > 0 && this.num <= 255) {
           this.i = Number(this.num);
           this.num = '';
         }
         
      }
      
      
   }
}
</script>
<style scoped>
   .wrap {
     text-align: center;
   }

   .taskDescr {
     font-size:16px;
   }

   .learnCard {
     text-align:center;
     position: relative;
   }
  
   .counter {
     position: absolute;
     top:10px;
     right:10px;
   }
   .mainWord {
      text-align:center;
      margin: 20px 0;
      width: 100%;
      color:#fff;
   }
   .searchNum {
      display:flex;
     
   }
   .searchNumInp {
      width: 80px;
      height: 30px;
   }
    @media screen and (max-width:600px) {
       h1 {
         font-size:20px;
       }
    }
</style>






