<template>
   <div class="wrap1">
      <h1>Основные неправильные глаголы</h1>
       <v-layout>
         <v-flex xs12 sm6 offset-sm3>
            <v-card>
               <v-card-title primary-title class="cardTitle">
                  <div class="searchNum">
                     <v-text-field
                        label="1-255"
                        single-line
                        Regular
                        v-model="num"
                        class="searchNumInp"
                     ></v-text-field ><v-btn @click="toFind" class="searchNumBtn" dark color="indigo">Перейти</v-btn>
                  </div>
                  <span class="counterWords">({{i}}/255)</span>
                  <h3 dark class="red mainWord headline mb-0">{{arr[i][3]}}</h3>
               </v-card-title>
               <v-card-text>
                  <div>
                     <div class="verb">
                        <h3>1-я форма(настоящее время)</h3>
                        <p>
                           <v-icon 
                           @click="play(arr[i][0])"  class="sound mr-2">volume_up</v-icon>
                           <span ref="sound1">{{ arr[i][0] }}</span>
                        </p>
                     </div>
                     <div class="verb">
                        <h3>2-я форма(прошедщее время)</h3>
                         <p>
                            <v-icon @click="play( arr[i][1])"  class="sound mr-2">volume_up</v-icon>
                            <span>{{ arr[i][1] }}</span>
                         </p>
                     </div>
                     <div class="verb">
                        <h3>3-я форма(причастие)</h3>
                         <p>
                            <v-icon @click="play(arr[i][2])"  ref="sound3" class="sound mr-2">volume_up</v-icon>
                            <span ref="sound3">{{ arr[i][2] }}</span>
                         </p>
                     </div>
                  </div>
                  
                  <v-divider></v-divider>
               </v-card-text>
               <v-btn @click="reduceCouter"  color='red'>
                  <v-icon color='white'>arrow_left</v-icon>
               </v-btn>
               <v-btn @click="raiseCounter"  color="red">
                  <v-icon color='white'>arrow_right</v-icon>
               </v-btn>
            </v-card>
         </v-flex>
        </v-layout>
   </div>
</template>

<script>
import json from '../verbs.json'
export default {
  data() {
     return {
        i:1,
        arr: '',
        num: ''

     }
  },
  created() {
      let strFromJson = JSON.stringify(json);
      this.arr = JSON.parse(strFromJson);
   },
   methods: {
      raiseCounter() {
         if (this.i == 255) {
            this.i = 1;
         } else {
            this.i++;
         }
         

      },
      reduceCouter() {
         this.i--
         if (this.i == 0) {
            this.i = 255;
         }
      },
      play(val) {
         let sound = val;
         /* eslint-disable-next-line  */
         responsiveVoice.speak(sound);
         /* eslint-disable-next-line no-console */
         console.clear();
      
      },
      toFind() {
         if (this.num > 0 && this.num <= 255) {
           this.i = this.num;
           this.num = '';
         }
         
      }
   }
}
</script>
<style scoped>
   
   .wrap1 {
      text-align: center;
   }
   .wrap1 h1 {
      margin-bottom:20px;
   }
   .cardTitle {
      position: relative;
   }
   .cardTitle h2{
     margin: 20px auto;
   }
   .counterWords {
     position: absolute;
     top:10px;
     right:10px;
   }
   .mainWord {
      text-align:center;
      margin: 20px 0;
      width: 100%;
      color:#fff
   }
   .verb span {
      color:#009688;
      font-size:30px;
   }
   .sound {
      cursor: pointer;
   }
   .searchNum {
      display:flex;
     
   }
   .searchNumInp {
      width: 80px;
      height: 30px;
   }
   @media screen and (max-width:489px) {
       h1 {
         font-size:20px;
       }
    }
</style>


